import React, { useContext, useEffect } from "react";
import "./WeatherCurrent.css";
import { WeatherContext } from "../../App";

function WeatherCurrent() {
  const { weatherData, setWeatherData } = useContext(WeatherContext);
  useEffect(() => {
    //console.log(weatherData)
  }, [weatherData, setWeatherData]);
  console.log("wether", weatherData);

  function getWindDirection(deg) {
    if (deg >= 337.5 || deg < 22.5) {
      return "";
    } else if (deg >= 22.5 && deg < 67.5) {
      return "-";
    } else if (deg >= 67.5 && deg < 112.5) {
      return "";
    } else if (deg >= 112.5 && deg < 157.5) {
      return "-";
    } else if (deg >= 157.5 && deg < 202.5) {
      return "";
    } else if (deg >= 202.5 && deg < 247.5) {
      return "-";
    } else if (deg >= 247.5 && deg < 292.5) {
      return "";
    } else {
      return "-";
    }
  }

  return (
    <section className="weatherCurrent">
      <div className="weatherNow">
        <p id="temperatureNow">
          {weatherData?.temperature !== undefined
            ? `${weatherData.temperature}`
            : "..."}
        </p>
        <img
          id="imageWeatherNow"
          alt=" "
          src={
            weatherData?.iconUrl
              ? weatherData.iconUrl
              : `${process.env.PUBLIC_URL}/images/index.png`
          }
        />
      </div>
      <p id="feelsLike">
         :{" "}
        {weatherData?.feelsLike !== undefined
          ? `${weatherData.feelsLike}`
          : "..."}
      </p>
      <p id="description">
        {weatherData?.description ? weatherData.description : "..."}
      </p>
      <div className="windNow">
        <p id="windSpeed">
          :{" "}
          {weatherData?.windSpeed !== undefined
            ? `${weatherData.windSpeed} /`
            : "..."}
        </p>
        <p id="windDirection">
        {weatherData?.windDeg !== undefined
          ? getWindDirection(weatherData.windDeg)
          : "..."}
      </p>
      </div>
    </section>
  );
}

export default WeatherCurrent;
