import React, {useEffect, useContext} from "react"
import WeatherDetail from "../WeatherDetail/WeatherDetail"
import './WeatherExtraDetails.css'
import { WeatherContext } from "../../App";
import { FireRiskContext } from "../../context/FireContext";


function WeatherExtraDetails() {
    const {weatherData, setWeatherData} = useContext(WeatherContext);
        useEffect(() => {
    //console.log(weatherData)
    }, [weatherData, setWeatherData]);

    const { fireRiskLabel } = useContext(FireRiskContext);
    console.log("f", fireRiskLabel);

    const getCloudiness = (cloudiness) => {
        if (cloudiness <= 20) return "";
        if (cloudiness <= 50) return "";
        if (cloudiness <= 70) return " ";
        if (cloudiness <= 90) return "";
        return "";
    };

    const getMoonPhase = (date) => {
        const synodicMonth = 29.5305882; //     
        const newMoonDate = new Date('2025-01-29'); //   
    
        const diffInDays = (date - newMoonDate) / (1000 * 60 * 60 * 24); //             
        const daysSinceNewMoon = diffInDays % synodicMonth; //         
    
        if (daysSinceNewMoon < 1.84566) {
            return '';
        } else if (daysSinceNewMoon < 5.53699) {
            return ' ';
        } else if (daysSinceNewMoon < 9.22831) {
            return ' ';
        } else if (daysSinceNewMoon < 12.91963) {
            return ' ';
        } else if (daysSinceNewMoon < 16.61096) {
            return '';
        } else if (daysSinceNewMoon < 20.30228) {
            return ' ';
        } else if (daysSinceNewMoon < 23.99361) {
            return ' ';
        } else {
            return ' ';
        }
    }

    const getVisibilityLevel = (visibility) => {
        if (visibility <= 100) return " ";
        if (visibility <= 500) return "";
        if (visibility <= 2000) return "";
        if (visibility <= 5000) return "";
        return "";
    };

    const weatherArray = [
  {
    type: "",
    value:
      weatherData?.humidity !== undefined
        ? `${weatherData.humidity}%`
        : "...",
    icon: "dropWhite.png",
  },
  {
    type: " ",
    value:
      weatherData?.uvIndex !== undefined ? weatherData.uvIndex : "...",
    icon: "index.png",
  },
  {
    type: "",
    value:
      weatherData?.soilTempCelsius !== undefined
        ? `${weatherData.soilTempCelsius}`
        : "...",
    icon: "soil.png",
  },
  {
    type: ",  . .",
    value:
      weatherData?.pressure !== undefined ? weatherData.pressure : "...",
    icon: "pressure.png",
  },
  {
    type: "",
    value:
      weatherData?.sunrise !== undefined ? weatherData.sunrise : "...",
    icon: "sunrise.png",
  },
  {
    type: "",
    value:
      weatherData?.sunset !== undefined ? weatherData.sunset : "...",
    icon: "sunset.png",
  },
  {
    type: ` ${
      weatherData?.cloudiness !== undefined ? weatherData.cloudiness + "%" : "..."
    }`,
    value:
      weatherData?.cloudiness !== undefined
        ? getCloudiness(weatherData.cloudiness)
        : "...",
    icon: "cloud.png",
  },
  {
    type: " ",
    value: weatherData ? getMoonPhase(new Date()) : "...",
    icon: "moon.png",
  },
  {
    type: ` ${
      weatherData?.visibility !== undefined
        ? (weatherData.visibility / 1000).toFixed(1) + " "
        : "..."
    }`,
    value:
      weatherData?.visibility !== undefined
        ? getVisibilityLevel(weatherData.visibility)
        : "...",
    icon: "visibility.png",
  },
  { type: "", value: `${(fireRiskLabel * 100).toFixed(0)}%`, icon: "fireWhite.png"}
];
    
    return (<section className="weatherExtraDetails">
        {weatherArray.map((detail, index) => (
            <WeatherDetail key={index} detail={detail}/>
        ))}
    </section>)
}

export default WeatherExtraDetails